<template lang="html">
  <div class="per-info-line" @click="goTo(pathname)">
    <img class="line-left" :style="{'width': width, 'height': height}" :src="icon" alt="">
    <div class="line-right">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    icon: {
      type: String,
      required: true
    },
    pathname: {
      type: String
    },
    width: {
      type: String,
      default: '.32rem'
    },
    height: {
      type: String,
      default: '.32rem'
    }
  },
  methods: {
    goTo (pathname) {
      pathname && this.$router.push(this.pathname)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';

.per-info-line {
  background: @whiteColor;
  display: table;
  width: 100%;
  line-height: .88rem;
  padding: 0 .22rem;
  border-bottom: 1px solid @lineColor;
  .line-left {
    vertical-align: middle;
    margin-right: .2rem;
  }
  .line-right {
    display: table-cell;
    overflow: hidden;
    width: 100%;
    vertical-align: middle;
  }
}
.per-info-line:last-child {
  border-bottom:none;
}
</style>



