<template lang="html">
  <div class="">
    <default-head header="企业信息" path="/companyCard"></default-head>
    <div class="person-info">
      <input type="hidden" v-model="companyInfo.facePic" name="facePic" data-vv-name="素颜照" v-validate="'required'">
      <person-head @submit-picname="getPicName" headMsg="企业logo" text="公司名称"
        data-vv-name="公司名称" v-validate="'required'" data-vv-value-path="currValue"
        :realName="companyInfo.name" :headImg="companyInfo.facePic || ''"
        notice="请上传企业logo或企业相关的图片"
      ></person-head>
      <div class="info-box">
        <info-line :icon="icons[0]">
          <div class="text-box" >
            <flm-form-input v-model="companyInfo.mobile" data-vv-name="手机" v-validate="'required'" name="mobile"
            label="手机" width="0.94rem" placeholder="未登录" textAlign="right"></flm-form-input>
          </div>
        </info-line>
        <info-line :icon="icons[1]">
          <div class="text-box" >
            <flm-form-input v-model="companyInfo.telephone"
            data-vv-name="座机" label="座机" width="0.94rem" placeholder="选填" textAlign="right"></flm-form-input>
          </div>
        </info-line>
      </div>
      <div class="info-box">
        <info-line :icon="icons[2]" @click.native="storeCompanyInfo('address')">
          <div class="text-box" >
            <div class="text-left">公司地址</div>
            <div class="text-right">{{commonAdress || '未设置'}}</div>
            <input type="hidden" v-model="commonAdress" name="commonAdress" data-vv-name="常驻地址" v-validate="'required'">
          </div>
        </info-line>
        <info-line :icon="icons[3]" @click.native="storeCompanyInfo('area')">
          <div class="text-box" >
            <div class="text-left">推广地域</div>
            <div class="text-right">{{zone || '未设置'}}</div>
            <input type="hidden" v-model="zone" name="commonAdress" data-vv-name="推广地域" v-validate="'required'">
          </div>
        </info-line>
      </div>
      <foot-btn @click.native="submitCompanyinfo"></foot-btn>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import personHead from '@/components/personCompany/personHead'
import infoLine from '@/components/common/infoLine'
import footBtn from '@/components/common/footBtn'
import mobile from '@/assets/img/personCompany/s02@3x.png'
import phone from '@/assets/img/personCompany/s03@3x.png'
import address from '@/assets/img/personCompany/s04@3x.png'
import area from '@/assets/img/personCompany/s05@3x.png'
export default {
  components: { defaultHead, personHead, infoLine, footBtn },
  data () {
    return {
      icons: [mobile, phone, address, area],
      companyInfo: {
        userId: 1,
        id: 1,
        facePic: '',
        name: '',
        mobile: '',
        telephone: '',
        hasAddress: 0,
        hasZone: 0,
        province: '',
        city: '',
        area: '',
        lat: 0,
        lon: 0
      },
      upload: {
        fileName: '',
        fileBase64: ''
      }
    }
  },
  computed: {
    commonAdress () {
      let location = JSON.parse(sessionStorage.getItem('companyPosition'))
      // 设置经纬度
      if (location) {
        // 选择新的地址设置相关信息
        sessionStorage.removeItem('companyPosition')
        this.companyInfo.hasAddress = 1
        return '已设置'
      } else {
        return this.companyInfo.hasAddress > 0 ? '已设置' : ''
      }
    },
    zone () {
      var companyArea = sessionStorage.getItem('companyArea')
      if (companyArea) {
        this.companyInfo.hasZone = 1
        sessionStorage.removeItem('companyArea')
        return '已设置'
      } else {
        return this.companyInfo.hasZone > 0 ? '已设置' : ''
      }
    },
    userId () {
      return utils.userId()
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    initPage () {
      if (!this.isLogin()) return
      let companyInfo = sessionStorage.getItem('companyInfo')
      let companyUpload = sessionStorage.getItem('companyUpload')
      if (companyInfo) {
        sessionStorage.removeItem('companyInfo')
        this.companyInfo = JSON.parse(companyInfo)
      }
      if (companyUpload) {
        sessionStorage.removeItem('companyUpload')
        this.upload = JSON.parse(companyUpload)
      }
      if (!companyInfo) {
        this.userId && this.ajaxGetCompanyInfo({userId: this.userId})
      }
    },
    storeCompanyInfo (sign) {
      sessionStorage.setItem('companyInfo', JSON.stringify(this.companyInfo))
      sessionStorage.setItem('companyUpload', JSON.stringify(this.upload))
      // 选取地址前传递标志位
      if (sign === 'address') this.$router.push({ path: 'cardaddress', query: { sign: 'company' } })
      if (sign === 'area') this.$router.push({ path: 'area', query: { sign: 'company' } })
    },
    getPicName (data, realName) {
      if (data.length > 0) {
        this.upload.fileName = data[0].fileName
        this.upload.fileBase64 = data[0].base64Img
        this.companyInfo.facePic = data[0].imgData
      }
      this.companyInfo.name = realName
    },
    submitCompanyinfo () {
      if (!this.isLogin()) return
      this.$validator.validateAll().then(() => {
        // if (!this.companyInfo.facePic) {
        //   minutils.Toast({ message: '素颜照不能为空', position: 'top' })
        //   return
        // }
        if (this.upload.fileBase64) {
          this.ajaxUploadPic(this.upload)
        } else {
          this.ajaxSaveCompanyInfo(this.companyInfo)
        }
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    ajaxGetCompanyInfo (data) {
      minutils.IndicatorOpen('加载中...')
      api.getCompanyInfo(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess && result.data) {
          Object.keys(this.companyInfo).forEach(k => {
            this.companyInfo[k] = result.data[k]
          })
        }
      })
    },
    ajaxSaveCompanyInfo (data) {
      minutils.IndicatorOpen('上传中...')
      return api.saveCompanyInfo(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          // sessionStorage.removeItem('companyPosition')
          // sessionStorage.removeItem('companyInfo')
          minutils.MessageBox({message: '保存企业信息成功'})
        }
      })
    },
    ajaxUploadPic (data) {
      api.uploadPic(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.companyInfo.facePic = result.data
          this.ajaxSaveCompanyInfo(this.companyInfo)
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';

.person-info {
  padding-top: .88rem;
}
.info-box {
  margin-top: .3rem;
}
.text-box {
  display: table;
  table-layout: fixed;
  width: 100%;
  .text-left {
    display: table-cell;
    font-size: .32rem;
    color: @defaultColor;
    width: 1.5rem;
  }
  .text-right {
    display: table-cell;
    overflow: hidden;
    color: @lightColor;
    font-size: .28rem;
    text-align: right;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>



