<template lang="html">
  <div class="person-head">
    <div class="pic-box">
      <label for="per-img">
        <span class="notice">{{headMsg}}</span>
        <img :src="headImg || defaultimg" alt="">
        <input @change="changeFile" accept="image/*" type="file" id="per-img" name="" value="" >
      </label>
      <div class="msg">
        {{notice}}
      </div>
    </div>
    <div class="name-line">
      <flm-form-input v-model="currValue" @change="$emit('submit-picname', data, currValue)"
      :label="text" width="1.58rem" placeholder="必填" textAlign="right"></flm-form-input>
    </div>
  </div>
</template>

<script>
import defaultimg from '@/assets/img/personCompany/ph2@3x.png'
import utils from '@/utils/utils'
export default {
  props: {
    realName: {
      type: String,
      default: ''
    },
    headImg: {
      type: String,
      default: defaultimg
    },
    headMsg: {
      type: String,
      required: true
    },
    text: {
      type: String,
      required: true
    },
    notice: {
      type: String,
      required: true
    }
  },
  watch: {
    realName (val) {
      this.currValue = val
    }
  },
  data () {
    return {
      defaultimg: defaultimg,
      currValue: this.realName,
      data: []
    }
  },
  methods: {
    changeFile (e) {
      let self = this
      utils.uploadPic(e.target.files, (data) => {
        self.data = data
        self.$emit('submit-picname', data, self.currValue)
      })
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
  .person-head {
    background: @whiteColor;
    .pic-box {
      text-align: center;
      position: relative;
      padding: .3rem .22rem;
      border-bottom: 1px solid @lineColor;
      .notice {
        display: inline-block;
        position: absolute;
        top: .3rem;
        left: .22rem;
        font-size: .32rem;
        color: @defaultColor;
      }
      label {
        display: inline-block;
      }
      input[type=file] {
        display: none;
      }
      img {
        width: 2.2rem;
        height: 2.5rem;
      }
      .msg {
        margin-top: .3rem;
        text-align: left;
        color: @lightColor;
        font-size: .28rem;
        line-height: .44rem;
      }
    }
    .name-line {
      overflow: hidden;
      line-height: .88rem;
      padding: 0 .22rem;
      background: @whiteColor;
      font-size: .32rem;
      .text {
        font-size: .32rem;
        float: left;
      }
      .hold {
        float: right;
        font-size: .28rem;
        color: @lightColor;
      }
    }
  }
</style>



